<template>
  <div class="notice-list">
    <van-nav-bar title="平台公告" left-text="" left-arrow @click-left="$router.back()" />

    <notice-list-card
      v-for="data in state"
      :key="data.id"
      :title="data.title"
      :content="data.content"
      :updatetime="data.updatetime"
      :id="data.id"
    />

    <p class="notice-list__end">没有更多啦</p>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from "vue"
import { NavBar, Toast } from "vant"
import NoticeListCard from "./NoticeListCard.vue"
import ProductService, { Notice } from "@/services/product"

export default defineComponent({
  name: "NoticeList",
  components: {
    NoticeListCard,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
  },
  setup() {
    let state = ref<Notice[]>([])
    let getNotice = () => {
      const loadingToast = Toast({ type: "loading", forbidClick: true, loadingType: "spinner" })
      ProductService.getNotice()
        .then(({ status, data, message }) => {
          if (status !== 200) {
            Toast(message)
            return
          }
          state.value = data
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    onMounted(getNotice)

    return { state }
  },
})
</script>
