<template>
  <div class="notice-list-detail">
    <van-nav-bar title="平台公告" left-text="" left-arrow @click-left="$router.back()" />
    <div class="notice-list-detail__top">
      <div class="notice-list-detail__top__title">{{ state.title }}</div>
      <div class="notice-list-detail__top__time">{{ state.updatetime }}</div>
    </div>
    <div class="notice-list-detail__bottom">{{ state.content }}</div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { NavBar, Toast } from "vant"
import ProductService, { Notice } from "@/services/product"
import { useRoute } from "vue-router"

export default defineComponent({
  name: "NoticeListDetail",
  components: {
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
  },
  setup() {
    let state = ref<Notice>({})
    let route = useRoute()
    let type = "2"

    onMounted(() => {
      ProductService.getNoticeDetail(type, route.params.id as string).then(
        ({ status, data, message }) => {
          if (status !== 200) {
            Toast(message)
            return
          }
          state.value = data
        },
      )
    })

    return { state }
  },
})
</script>
